/*
Sidebar Says Flex right

Creates a two column layout using flexbox.

Markup:
  <div className="container">

    <div class="column-main tile"></div>
    
    <div class="column-sidebar">
      <div class="tile"></div>
      <div class="tile"></div>
    </div>

  </div>

Weight: 1

Styleguide Utilities.pancake-right--Flex
*/

.tile
{
  padding: 1em
}

.container
{
  display: flex;
  flex-direction: column;
}



.column-main
{
  flex: 70%;
}

.column-sidebar
{
  flex: 30%;
}


@media (min-width: 600px)
{  

  .column-main
{
  flex:  0 1 70%;
  margin-left: 30%;
}
}
