:root
{
  //BlACKS
  --pure-black: hsla(0, 0%, 0%, 1);
  --vampire-Black: hsla(0, 0%, 4%, 1);
  --chinese-Black: hsla(0, 0%, 7%, 1);
  --eeri-Black: hsla(0, 0%, 10%, 1);
  --raisin-Black: hsla(0, 0%, 13%, 1);

  //Blue
  --Sky-Blue: hsla(210, 100%, 50%, 1);
  --Deep-Blue: hsla(210, 100%, 15%, 1);

  //Greys
  --grey-chrome: hsla(210, 10%, 5%, 1);
  --grey: hsla(0, 0%, 60%, 1);
  --white-grey: hsla(0, 0%, 93%, 1);
  --light-grey: hsla(0, 0%, 85%, 1);
  --peach-grey: hsla(0, 10%, 50%, 0.5);

  //Yellow
  --dark-yellow: hsla(56, 100%, 20%, 1);

   //white
   --white: hsla(0, 0%, 95%, 1);
}