.about
{
  font-size: 1rem;
  padding: 0.5em;
  margin-left: 1em;
  margin-right: 1em;
  text-align: justify;
  line-height: 1.5;
}

.about + ol
{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(50%, 1fr));
  text-align: left;
  list-style-type: none;
  padding-left: 0.2em;
  margin-left: 1.5em;
}

.about + ol > li
{
  padding: 0.2em;

}

.about + ol > li > span
{
  color: var(--white);
  font-size: 0.9rem;
  font-weight: 400;
  background-color: var(--Deep-Blue);
  padding: 0.4em;
  box-sizing: content-box;
  margin-right: 1em;
  height: 1em;
  width: 1em;
  border-radius: 50%;
}

.about + ol > li + li 
{
margin-top: 0;
}

.about + ol > li + li + li
{
  margin-top: 0.8em;
}

@media (min-width: 600px)
{
  .about
  {
    font-size: 0.78rem;
  }

  .about + ol 
  {
    text-align: justify;
    font-size: 0.75rem;
  }

  .about + ol > li > span
  {
    font-size: 0.8rem;
  }
}