.rating
{
  display: flex;
  padding-left: 0.5em;
  padding-right: 0.5em;
  font-size: 0.9rem
}

.rating + .rating
{
  margin-top: 0;
}

.rating__field
{
  font-weight: 400;
}

.rating__ratings
{
  margin-left: auto;
  display: flex;
  align-items: center;
}

.rating__ratings > span + span
{
  margin-top: 0;
  margin-left: 0.2em;
}

.rating-bullet
{
  width: 0.85em;
  height: 0.85em;
  display: inline-block;
  border-radius: 50%;
  border: 1px solid var(--eeri-Black);
}

.rating__darkGrey
{
  background-color: hsla(210, 100%, 20%, 1);
}

.rating__lightGrey
{
  background-color: var(--grey-chrome);
}

@media(min-width: 600px)
{
  .rating__field
  {
    font-size: 0.8rem;
  }
}