.text-center
{
  text-align: center !important;
}

.l-float-left
{
  float: left;
}

.l-float-right
{
  float: right;
}



.clearfix::before,
.clearfix::after
{
  content: " ";
  display: table;
}

.clearfix::after
{
  clear: both;
}

.hidden
{
  display: none !important;
}

.p-relative
{
  position: relative;
}

.p-fixed
{
  position: fixed;
}


.p-absolute
{
  position: absolute;
}

.p-sticky
{
  position: sticky;
}

.p-static
{
  position: static;
}

.l-flex
{
  display: flex;
}


.l-inline
{
  display: inline;
}

.spc-container
{/* super centered: target the parent */
  display: grid;
  place-items: center;
}

.ztp
{
  margin-top: 0 !important;
}

.dcp
{
  max-width: 1100px;
  margin: 0 auto;
  padding: 0.5em;
}
