.column-sidebar
{
  background-image: var(--transparent-radial-black), var(--transparent-radial-black);
  padding: 1em;
  color: var(--grey);
  border-radius: 1em;

}

.portfolio-sidebar
{
  height: calc(100vh - 5em);
}

.portfolio-sidebar__image
{
  width: 100%;
}

.portfolio-sidebar__image > img
{
  position:relative;
  border-radius: 50%;
  object-fit: cover;
  height: 180px;
  width: 180px;
  box-shadow: var(--perfect-box-shadow);
}

.portfolio-sidebar__image::after
{
  position: absolute;
  background-image: radial-gradient( hsla(0, 10%, 4%, 0.5), hsla(0, 0%, 0%, 0.5));
  border-radius: 50%;
  content: "";
  line-height: 1.4;
  top: 0;
  left: 20%;
  right: 20%;
  bottom: 70%;
}

.portfolio-sidebar__name
{
/*   text-transform: uppercase; */
  color: var(--Deep-Blue);
}

.portfolio-sidebar__slogan
{
  color: var(--white);
}



.portfolio-sidebar__title > ul
{
  padding-left: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  list-style-type: none;
  text-transform: uppercase;
}

.portfolio-sidebar__title > ul > li
{

  padding: 0.5em 0.8em;
  border-radius: 1em;
  box-shadow: var(--perfect-box-shadow);
  color: var(--grey);
  background-image: var(--transparent-radial-black);
}

.portfolio-sidebar__title > ul > li + li
{
  margin-top: 1em;
}

.findMe
{
  color: var(--Deep-Blue);
  border-bottom: 2px solid var(--Deep-Blue);
  text-align: center;
  margin-bottom: 0;
  text-transform: uppercase;
  margin-left: auto;  
  margin-right: auto;  
  width: 80%;
  padding-bottom: 1em;
}

.at
{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.4rem;
  font-weight: 900;
}

.social-container
{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(10%, 1fr));
  gap: 0.5em;
}

.social
{

/*   padding: 0.3em; */
/*   border-radius: 30%; */
/*   box-shadow: var(--perfect-box-shadow); */
/*   background-color: var(--grey-chrome); */
  box-sizing: content-box;
}

.social > a > img
{
  object-fit: cover;

}

.social + .social
{
  margin-top: 0;
}

@media(min-width: 600px)
{
  .column-sidebar
  {
    margin-right: 1em;
    position: fixed;
    right: 70%;
    left: 1em;
  }

  .portfolio-sidebar
{
  height: calc(100vh - 4em);
}

  .portfolio-sidebar__name 
  {
    font-size: 1.5rem;

  }

  .portfolio-sidebar__slogan
  {
    font-size: 0.8rem;
    color: var(--white);
  }

  .portfolio-sidebar__title > ul > li
  {
    text-align: center;
    text-decoration: none;
    font-size: 0.9rem;
  }


}
