:root
{
  box-sizing: border-box;
}

*,
*::before,
*::after
{
 box-sizing: inherit;/*tells other elements and pseudo elements to inherit their box sizing*/
}

body * + *
/*Lobotomized Owl selector (used to space elements within containers)*/
{
 margin-top: 1.5em;
}

/* .third-party-component
{
 box-sizing: border-box;
} */

body
{
  margin: 0;
  padding: 0;
  font-family: var(--Exo2);
  line-height: 1.4;
  letter-spacing: 0.02em;
  color: var(--grey);
  background-image: var(--transparent-radial-black), linear-gradient(360deg, var(--Sky-Blue), var(--Sky-Blue));
}

#react-root
{
  margin: 0;
}

img
{
  width: 100%;
 max-width: 100%;
}


