.education
{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax( 300px, 1fr));
  gap: 1.5em 1em;
}

.qualification
{
  padding: 0.5em;
  font-size: 0.9rem;
  border-radius: 1em;
  background-image: var(--transparent-radial-black), var(--transparent-radial-black); 
}

.qualification + .qualification
{
  margin-top: 0;
}

.qualification > div
{
  display: flex;
  margin-top: 0.5em;
}

.qualification > div > span
{
  flex: 35%;
  padding: 0.2em;
  color: var(--white);
}

.qualification > div > span + span
{
  margin-top: 0;
  flex: 60%;
  text-align: left;
  color: var(--grey);
}

.qualification-title
{ 
  background-image: var(--transparent-radial-black), var(--transparent-radial-black);
  color: var(--Sky-Blue);
  font-size: 0.9rem;
  padding: 0.5em 0.8em;
  border-radius: 1em;
  margin: 0 0 1.5em 0;
  font-weight: 500;
  box-shadow: inset var(--perfect-box-shadow);
  text-align: center;
  text-transform: uppercase;
}

.next
{
  width: 1.875em;
  object-fit: scale-down;
  cursor: pointer;
  margin-right: 0.5em;
}

.nqf, .count
{
  color: hsla(337, 91%, 47%, 1);
  background-color: var(--grey-chrome);
  font-weight: 700;
  width: 1.25em;
  height: 1.25em;
  text-align: center;
  display: inline-block;
  padding: 0.3em;
  box-sizing: content-box;
  border-radius: 50%;

}

.count
{
  color: var(--white);
  background-color: var(--Deep-Blue);
  font-weight: 400;
  padding: 0.3em;
}

.qualification-major > span
{
  border-radius: 1rem;
  background-color: var(--grey-chrome);
  font-size: 0.75rem;
  color: var(--Sky-Blue) !important;
  font-weight: 600;
}



.status
{
  font-weight: 700;
  padding: 0.5em;
  border-radius: 1em;
  background-color: var(--grey-chrome);
}

.complete
{
  color: hsla(120, 100%, 30%, 1) !important;
}

.enrolled
{
  color: hsla(60, 100%, 30%, 1) !important;
}

@media(min-width: 600px)
{
  .qualification-title, .qualification
  {
    font-size: 0.8rem;
  }

  .qualification > div
  {
    margin: 0.2em
  }

  .qualification > div > span
  {
    flex: 30%;
    padding: 0.05em;
  }

  .qualification-major > span
{
  font-weight: 400;
}

}