:root
{
  --Exo: 'Exo' , sans-serif;
  --Exo2: 'Exo 2', sans-serif;
}

@font-face
{
  font-family: 'Exo', sans-serif;
  src: local('Exo'), url('https://fonts.googleapis.com/css2?family=Exo&display=swap') format("truetype");
  font-weight: 1 900;
}

@font-face {
  font-family: 'Exo 2', sans-serif;
  src: local('Exo 2'), local('Exo-2'), url('https://fonts.googleapis.com/css2?family=Exo+2&display=swap') format("truetype");

  font-weight: 1 900;
}
