.portfolio-main
{
  border-radius: 1em;
  background-image: var(--transparent-radial-black);
}

.app
{
  margin: 1em;
}

.heading
{
  position:relative;
  color: var(--white-grey);
  background-color: hsla(0, 5%, 10%, 0.5);
  box-shadow: var(--perfect-box-shadow);
  font-weight: 450;
  padding: 0.5em 0.8em;
  line-height: 1.4;
  border-radius: 0.9em;
  display: inline-block;
  text-transform: uppercase;
}

.heading::after
{
  position: absolute;
  background-image: radial-gradient( hsla(210, 10%, 4%, 0.5), hsla(0, 0%, 0%, 1));

  padding: 0.5em 0.8em;
  border-radius: 0.9em;
  content: "";
  line-height: 1.4;
  top: 0em;
  left: 0em;
  right: 0em;
  bottom: 0em;
}

.portfolio-main__about :first-child
{
  margin-top: 0;
}

.subheading
{
  background-image: var(--transparent-radial-black), var(--transparent-radial-black);
  color: var(--Deep-Blue);
  font-size: 0.9rem;
  padding: 0.5em 0.8em;
  border-radius: 1em;
  margin: 0;
  font-weight: 600;
  box-shadow: inset var(--perfect-box-shadow);
  text-transform: uppercase;
}


@media (min-width: 600px)
{
  .portfolio-main
  {
    margin-top: 0;
    height: calc(100vh - 2em);
  }

  .heading, .subheading
  {
    font-size: 0.8rem;
  }
}